
import SvgTemplate from "./components/svg-template.vue";
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'

export default {
name: "app",
data() {
return {
flag: true,
};
},
mounted() {
this.flag = this.IsPC();
window.onresize = () => {
console.log(123)
this.flag = this.IsPC();
}
},
computed: {
animateName() {
return this.$store.state.animate_name;
},
navIndex() {
return this.$store.state.nav_index;
},
isNotFirst() {
return this.$store.state.is_not_first;
},
},
components: {
SvgTemplate,
Tabbar,
TabbarItem,
},
methods: {
IsPC() {
var userAgentInfo = navigator.userAgent;
var Agents = [
"Android",
"iPhone",
"SymbianOS",
"Windows Phone",
"iPad",
"iPod",
];
var flag = true;
for (var v = 0; v < Agents.length; v++) {
if (userAgentInfo.indexOf(Agents[v]) > 0) {
flag = false;
break;
}
}
if(document.body.clientWidth<500){
flag = false;
}
return flag;
},
},
};



