import Vue from 'vue'
import App from './App'
import router from './router'
import store from './store/index.js'
import './assets/lib/flexible.js'
import './assets/lib/Chart'
/**vux*/
import ToastPlugin from 'vux/src/plugins/toast/index.js'

Vue.use(ToastPlugin)

Vue.config.productionTip = false

new Vue({
el: '#app',
router,
store,
template: '<App/>',
components: { App }
})



