
import Scroller from 'vux/src/components/scroller/index.vue'

import GestureMobile from '../../assets/lib/GestureMobile'
import types from '../../store/mutation-types'
import CountUp from '../../assets/lib/countUp'
import Util from '../../assets/lib/Util'
export default {
name: 'home',
data: function () {
return {
is_open: false,
total_balance: 0
}
},
components:{
Scroller,
},
created () {
this.gestureMobile();
this.setNavIndex();
this.fetchBalance();
},
methods: {
/***/
gestureMobile () {
this.$nextTick(() => {
let _this = this;
GestureMobile(this.$el,{
upCallBackFun () {
_this.is_open = true;
},
downCallBackFun () {
_this.is_open = false;
}
});
})
},
/***/
fetchBalance () {
this.total_balance = Util.TotalBalance.query();
this.$nextTick(() => {
new CountUp("total_balance", 0, this.total_balance, 2, 2).start();
})
},
/***/
setNavIndex () {
this.$store.commit(types.SET_NAV_INDEX,'1')
}
}
}



