
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import Calendar from 'vux/src/components/calendar/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'

import headTitle from '../../../components/head-title.vue'
import Tool from '../../../assets/lib/Tool'
import Util from '../../../assets/lib/Util'
import types from '../../../store/mutation-types'
export default {
name: 'consumption',
created () {
console.log(this.$route)
this.setNavIndex();
if(this.$route.query.account_type) {
this.account_type = [this.$route.query.account_type]
}
},
components: {
Calendar,
Datetime,
XInput,
PopupPicker,
headTitle
},
data () {
return {
is_btn_active: false,
sum_value: '',
remarks_value: '',
account_type_arr: [['','', '', '', '', '', '','']],
account_type:[''],
date_value: 'TODAY',
time_value: Tool.format('hh:mm')
}
},
methods: {
/***/
subBill () {
if(!this.sum_value) {
this.showMsg('');
return;
}
if(isNaN(+this.sum_value)) {
this.showMsg('');
return;
}
var bill = {
_id: Date.parse(new Date()),
sum_value: this.sum_value,
date_value: this.date_value,
time_value: this.time_value,
remarks_value: this.remarks_value,
account_type: this.account_type,
billTypeNumber: this.billTypeNumber(this.account_type),
consumption_or_earn: 0
};
Util.Bill.save(bill);
this.showMsg('');
this.resetValue();
},
showMsg (msg) {
this.$vux.toast.show({
type: 'text',
width: 'auto',
text: msg,
position: 'top'
});
},
resetValue () {
this.sum_value = '';
this.remarks_value = '';
this.date_value = 'TODAY';
this.time_value = Tool.format('hh:mm');
},
setNavIndex () {
this.$store.commit(types.SET_NAV_INDEX,'2')
},
billTypeNumber ( account_type ) {
switch (account_type[0]){
case '':
account_type = 'sgls';
break;
case '':
account_type = 'cyhs';
break;
case '':
account_type = 'cxly';
break;
case '':
account_type = 'wsgw';
break;
case '':
account_type = 'shrc';
break;
case '':
account_type = 'cfsd';
break;
case '':
account_type = 'ylyw';
break;
case '':
account_type = 'qt';
break;
}
return account_type;
}
}
}



